import React from "react";

const Sresult = (props)=>{
    const img = `https://source.unsplash.com/600x400/?${props.name}`;
    return (
        
        <>
        
        <div className='img'>
        <img src= {img} alt="Searching......"/>
        </div>
        </>
    )

};

export default Sresult;